<template>
    <div class="container">
        <div class="root">
            <div class="inputContainer">
                <input class="input" type="text" placeholder="请输入关键字..." />
            </div>
            <div class="blogList">
                <div class="blogItem" v-for="blog in filteredBlogs" :key="blog.fileName">
                    <div class="title" @click="navToDetailPage(blog)">
                        {{ blog.title }}
                    </div>
                    <div class="blog-content-preview">
                        {{ blog.subtitle }}
                    </div>
                    <div class="blog-tags" v-if="blog.tags">
                        <div v-for="(tag) in blog.tags.split(',')" v-bind:key="tag" class="blog-tag">
                            {{ tag }}
                        </div>
                    </div>
                    <hr />
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import { useBlogStore } from '@/store/blogstore'
import { storeToRefs } from 'pinia'
export default {
    data() {
        return {
            blogs: [],
            filteredBlogs: []
        }
    },

    activated() {
        console.log('activated')
        //让input自动获取焦点
        this.$nextTick(() => {
            this.$el.querySelector('.input').focus()
        })
        const blogStore = useBlogStore()
        const { blogList } = storeToRefs(blogStore)
        this.blogs = blogList

        //监听input内容变化
        this.$el.querySelector('.input').addEventListener('input', (e) => {
            console.log(e.target.value)
            const value = e.target.value
            if (value) {
                this.filteredBlogs = this.blogs.filter(blog => {
                    // 判断blog.title是否包含关键字，不区分大小写
                    return (blog.title && blog.title.toLowerCase().includes(value.toLowerCase())) ||
                        (blog.subtitle && blog.subtitle.toLowerCase().includes(value.toLowerCase()))
                })
            } else {
                this.filteredBlogs = []
            }
        })

    },

    methods: {
        navToDetailPage(blog) {
            this.$router.push({
                name: 'detail',
                //传递blog参数到detail页面
                query: {
                    fileName: blog.fileName
                }
            })
        },
    }
}
</script>
